.card {
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 500;
  font-size: 15px;
  line-height: 21px;

  color: #fff;
  padding: 0;
  margin: 0;
  margin-bottom: 20px;

  position: relative;

  &:hover {
    color: #d58c51;
  }

  &__img {
    padding: 20px;
  }

  &__title {
    padding-left: 20px;
    padding-right: 20px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
  }

  &__text {
    padding-left: 20px;
    padding-right: 20px;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
  }
  &__top {
    &:hover {
      cursor: pointer;
      user-select: none;
    }
    &:active {
      animation: 0.2s tapped;
    }
  }
  &__bottom {
    position: absolute;
    bottom: 0;
    width: 250px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
  }

  &__price {
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
  }

  &__weight {
    font-weight: 300;
    font-size: 14px;
    line-height: 17px;
  }

  &__button {
    position: absolute;
    right: -5px;
  }
}

.showSale {
  position: absolute;
  right: 0;
  margin: 10px;
  filter: invert(100%);
}

@keyframes tapped {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}
