.selected {
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 30px;
  width: auto;
  &__card {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__img {
    width: 122px;
    height: 122px;
  }
  &__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    margin-left: 80px;
    cursor: pointer;
    &:hover {
      text-decoration: underline;
    }
  }
  &__cost {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;

    color: #d58c51;
    margin-right: 15px;
  }
}

.cart {
  &__header {
    max-width: 1300px;
    height: 180px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    &_name {
      display: flex;
      align-items: center;
      
    }
    &__products {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    &__text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 500;
      font-size: 17px;
      line-height: 21px;
      text-align: right;
  
      color: #ffffff;
    }
  
    &__btn {
      width: 50px;
      height: 50px;
      padding-left: 20px;
      cursor: pointer;
    }
    &_title {
      color: white;
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 700;
      font-size: 31px;
      line-height: 38px;
      /* identical to box height */
      
      text-transform: uppercase;
      margin-left: 70px;
    }

    .registration__button {
      position: relative;
      top: 3px;
    }
  }
  &__main {
    max-width: 830px;
    margin: 0 auto;

    height: calc((100vh - 100px) - 150px);
    overflow-y: auto;
  }

  &__footer {
    max-width: 100%;

    border-top: 1px solid #d58c51;
    background-color: #161516;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2;
    &_container {
      max-width: 830px;
      margin: 0 auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &_text {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 21px;
      line-height: 26px;
      /* identical to box height */

      text-transform: uppercase;

      color: #ffffff;
    }
    &_cost {
      font-family: 'Montserrat';
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      margin-left: 10px;

      color: #d58c51;
    }
  }
}
