.products {
  &__header {
    max-width: 1300px;
    height: 180px;
    margin: 0 auto;
    font-family: 'Montserrat', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 31px;
    line-height: 38px;
    /* identical to box height */

    text-transform: uppercase;
    color: #ffffff;

    display: flex;
    justify-content: space-between;
  }

  &__title {
    padding-top: 70px;
  }

  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    height: calc(100vh - 180px);
    overflow-y: auto;

  }

  .card {
    border: 1px solid #d58c51;
    width: 312px;
    height: 552px;
  }
}

.cart {
  &__products {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    text-align: right;

    color: #ffffff;
  }

  &__btn {
    width: 50px;
    height: 50px;
    padding-left: 20px;
    cursor: pointer;
  }
}

.registration__button {
  margin: 0 0 15px 20px;
}

