.btn {
  position: relative;
  top: 4px;
  background: #d58c51;
  color: #131313;
  border: 1px solid transparent;

  padding: 10px 40px 10px 40px;

  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  cursor: pointer;
  &:hover {
    opacity: 0.8;
  }
  &:active {
    opacity: 1;
  }
}
