.registration-page {
  background-image: url('../../public/images/bg/registration.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form {
  background-color: #fff;
  width: 460px;
  height: 340px;
  display: block;
  margin: 0 auto;
  position: relative;
  text-align: center;
  animation: 0.2s fade-in;

  &__href {
    position: absolute;
    right: 0;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    font-size: 11px;
    line-height: 13px;
    /* identical to box height */

    text-align: right;
    text-decoration-line: underline;

    color: #d58c51;
    margin: 10px 20px;

    cursor: pointer;
    user-select: none;

    &:hover {
      opacity: 0.7;
    }
    &:active {
      color: #161516;
    }
  }

  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 31px;
    line-height: 38px;
    /* identical to box height */

    text-transform: uppercase;

    color: #161516;
    margin-top: 35px;
    margin-bottom: 40px;
  }
  &__input {
    border: 1px solid #d58c51;
    border-radius: 61px;
    padding: 10px 20px;
    margin-bottom: 0px;
    width: 380px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    /* identical to box height */

    color: #161516;
  }
  &__label {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    font-size: 11px;
    line-height: 13px;
    /* identical to box height */

    color: #301411;
    display: flex;

    position: relative;
    top: -10px;
    left: 20px;
    margin-bottom: 0px;
    user-select: none;

    &:before {
      content: '';
      display: block;
      position: relative;
      top: 5px;
      width: 18px;
      height: 18px;
      color: #d58c51;
      border: 1px solid #d58c51;
      border-radius: 50%;
      z-index: 0;
    }

    &:after {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      background-color: #d58c51;
      border: 1px solid #d58c51;
      border-radius: 50%;

      transition: opacity 0.2s linear;
      opacity: 0;
      position: absolute;
      top: 9px;
      left: 4px;
      z-index: 1;
    }
  }
  &__checkbox {
    position: absolute;
    z-index: 2;
    width: 20px;
    height: 20px;
    top: 221px;
    left: 16px;
    opacity: 0;
    &:checked + .form__label::after {
      opacity: 1;
    }
  }

  &__text {
    margin: 10px 8px;
    height: 10px;
  }

  &__alert {
    position: relative;
    left: 40px;

    text-align: left;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 300;
    font-size: 8px;
    line-height: 10px;
    height: 10px;
    /* identical to box height */

    color: #ff0b0b;

    margin: 1px 0 5px 0;

    &_button {
      text-align: center;
      position: relative;
      left: 0px;
      top: -7px;
      margin-top: 0;
      margin-bottom: 5px;
    }
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}
