.button {
  &__add {
    border: 1px solid #d58c51;
    background-color: transparent;
    color: #fff;
    border-radius: 50%;
    cursor: pointer;
    width: 30px;
    height: 30px;
    margin: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    &:hover {
      background-color: #d58c51;
      border-color: #d58c51;
    }
    &:active {
      background-color: transparent;
      animation: clicked 0.2s;
    }
  }
  &__img {
    position: relative;
    left: 1px;
    top: 1px;
  }
}

@keyframes clicked {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
    background: transparent;
  }
  100% {
    transform: scale(1);
  }
}
