.info {
  background: #161516;
  background-blend-mode: screen;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: local, fixed;
  height: 100vh;
  @media screen and (max-width: 824px) {
    flex-direction: column;
    align-items: center;
    background: #161516;
  }

  &__header {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    max-width: 1300px;

    &_flex {
      max-width: 1300px;
      width: 1300px;
      height: 180px;
      font-family: 'Montserrat', sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 31px;
      line-height: 38px;
      /* identical to box height */

      text-transform: uppercase;

      color: #ffffff;

      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
  &__main {
    margin: 0 auto;
    max-width: 1024px;
    // width: 1200px;
    color: white;
    display: flex;
    justify-content: space-between;

    @media screen and (max-width: 824px) {
      flex-direction: column;
      align-items: center;
    }
  }
}

.main {
  &__img {
    width: 400px;
    height: 400px;
    margin-right: 30px;
  }
  &__title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 37px;

    color: #d58c51;
  }
  &__text {
    max-width: 520px;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
  }

  &__price {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 23px;
    line-height: 28px;
  }
  &__weight {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
  }
  &__bottom {
    display: flex;
    align-items: center;
    margin-top: 50px;
  }

  &__button {
    margin-left: 70px;
  }
}

.cart {
  &__products {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    text-align: right;

    color: #ffffff;
  }

  &__btn {
    width: 50px;
    height: 50px;
    padding-left: 20px;
    cursor: pointer;

    &:hover {
      opacity: 0.8;
      animation: 0.2s hovered;
    }
    &:active {
      opacity: 1;
    }
  }
}

.registration__button {
  margin: 0 0 15px 20px;
}
